<template>
  <div class="mdl-grid order">
    <div class="mdl-cell--6-col-desktop	mdl-cell--4-col-phone">
      <ul class="mdl-list">
        <li class="mdl-list__item mdl-list__item--two-line">
          <span class="mdl-list__item-primary-content">
            <i class="material-icons mdl-list__item-avatar">person</i>
            <strong>{{client.name}}</strong>
            <span class="mdl-list__item-sub-title">
              {{client.email}}
            </span>
            <span class="mdl-list__item-sub-title">
              {{client.address_line_1}}
            </span>
            <span class="mdl-list__item-sub-title">
              <strong>{{client.contact_number}}</strong>
            </span>
          </span>
        </li>
      </ul>
    </div>
    <div class="mdl-cell--6-col-desktop	mdl-cell--4-col-phone">
      <ul class="mdl-list">
        <li class="mdl-list__item mdl-list__item--two-line">
          <span class="mdl-list__item-primary-content">
            <strong>GST Number</strong>
            <span class="mdl-list__item-sub-title">
              {{client.gst_number}}
              </span>
          </span>
        </li>
        <li class="mdl-list__item mdl-list__item--two-line">
          <span class="mdl-list__item-primary-content">
            <strong>Procurement Number</strong>
            <span class="mdl-list__item-sub-title">
              {{procurement.procurement_number}}
              </span>
          </span>
        </li>
      </ul>
    </div>
    <div class="mdl-cell--12-col-desktop	mdl-cell--4-col-phone">
      <table class="mdl-data-table mdl-js-data-table">
        <thead>
          <tr>
            <th>Product</th>
            <th>Price</th>
            <th>Quantity</th>
            <th>Amount</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in lineItems" :key="item.product">
            <th>{{item.label}}</th>
            <th>{{item.price}}</th>
            <th>{{item.quantity}}</th>
            <th>{{ item.price * item.quantity }}</th>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="mdl-cell--12-col-desktop	mdl-cell--4-col-phone">
      <h4 class="text-center">Invoices</h4>
      <button
        class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary custom-button--right"
        @click="toggleFormView"
      >
      Create Invoice
      </button>
      sales invoice list

    </div>
    <div class="mdl-cell--12-col-desktop	mdl-cell--4-col-phone">
      <dialog-view ref="addInvoiceFormDialog">
        <div slot="dialog-body">
          <sales-procurement-invoice-view
            :procurement="procurement"
            ref="salesInvoiceForm"
            @orderComplete="orderComplete"
          ></sales-procurement-invoice-view>
        </div>
        <div slot="dialog-actions">
          <button
            class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
            @click="createInvoice"
          >
          Create
          </button>
        </div>
      </dialog-view>
    </div>
  </div>
</template>
<script>
import { SalesProcurementInvoiceView } from '@/apps/sales/components'
import { DialogView } from 'SHARED/dialog'

export default {
  name: 'procurement-edit-form',
  props: {
    procurement: {
      type: Object,
      required: true
    }
  },
  components: {
    SalesProcurementInvoiceView,
    DialogView
  },
  computed: {
    client () {
      return this.procurement.client || {}
    },
    lineItems () {
      return this.procurement.line_items || []
    }
  },
  methods: {
    toggleFormView (show) {
      if (show) {
        this.$refs.addInvoiceFormDialog.showModal()
      } else {
        this.$refs.addInvoiceFormDialog.closeModal()
      }
    },
    orderComplete () {
      this.toggleFormView(false)
    },
    createInvoice () {
      this.$refs.salesInvoiceForm.proceed()
    }
  }
}
</script>



